import React from 'react';
import { Slots, Slot } from './styledComponents';

const fabrics = [
  'dentelle',
  'cuir',
  'soie/mousseline',
  'tailleur',
  'maille',
];

const noneState = {};
fabrics.forEach((fabric) => { noneState[fabric] = false; });

class FabricSelector extends React.Component {
  constructor() {
    super();
    this.state = {
      noneSelected: true,
    };
  }

  toggleFabric(fabric) {
    this.setState({ [fabric]: !this.state[fabric] });
  }

  selectNone() {
    this.setState(noneState);
  }

  isNoneSelected() {
    return fabrics.filter(fabricIt => this.state[fabricIt]).length === 0;
  }

  render() {
    return (
      <Slots>
        {fabrics.map(fabric =>
          <Slot
            isSelected={this.state[fabric]}
            onClick={() => this.toggleFabric(fabric)}
            key={fabric}
          >{fabric}</Slot>,
        )}
        <Slot
          isSelected={this.isNoneSelected()}
          onClick={() => this.selectNone()}
        >aucun</Slot>
      </Slots>
    );
  }
}

export default FabricSelector;



